@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap");
.navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 2em;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.navbar nav .nav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}

.navbar nav .nav-list .nav-item {
  margin: 0 0.8em;
  cursor: pointer;
  opacity: 50%;
}

.navbar .nav-cta-container .nav-cta {
  background-image: -webkit-gradient(linear, left top, right top, from(#31d35c), to(#2bb7da));
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
  padding: 1em 1.8em;
  color: white;
  font-weight: 700;
  border-radius: 30px;
  border: none;
  font-size: 14px;
  cursor: pointer;
}

.navbar .nav-cta-container .hamburger {
  display: none;
}

@media screen and (max-width: 425px) {
  .navbar nav .nav-list {
    display: none;
  }
  .navbar .nav-cta-container .nav-cta {
    display: none;
  }
  .navbar .nav-cta-container .hamburger {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    cursor: pointer;
  }
}
