@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap");
.features-section {
  background-color: #f3f4f6;
  margin-top: -150px;
  padding: 4em 3em;
}

.features-section .features-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.features-section .features-header h2 {
  font-weight: 400;
  font-size: 2em;
  margin-bottom: 40px;
  color: #2d314d;
}

.features-section .features-header p {
  opacity: 50%;
  width: 50%;
  font-size: 18px;
  line-height: 1.5em;
  margin-bottom: 50px;
}

.features-section .feature-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

@media screen and (max-width: 425px) {
  .features-section {
    margin: 0;
  }
  .features-section .features-header {
    text-align: center;
  }
  .features-section .features-header p {
    text-align: center;
    width: 100%;
  }
  .features-section .feature-container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}
